.header {
  display: flex;
  min-height: 60px;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  background-color: #2455f4;
  color: white;
}

.header a {
  text-decoration: none;
  color: white;
  margin: 0 1vmax;
}

.header input[type="search"] {
  min-height: 40px;
  flex-basis: 30vmax;
  background-color: white;
  padding: 0 10px;
  border: none;
}

.header input[type="submit"] {
  min-height: 30px;
  min-width: 100px;
  margin: 10px;
  background-color: white;
  border: none;
  cursor: pointer;
}


#footer {
  display: flex;
  background-color: whitesmoke;
  color: black;
  box-sizing: border-box;
  padding: 1vmax;
}

.leftFooter {
  width: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.leftFooter > img {
  margin-top: 1vmax;
  width: 8vmax;
  cursor: pointer;
}

.leftFooter > p {
  margin-top: 1vmax;
  font-size: 1vmax;
}

.leftFooter > h4 {
  font-size: 1.5vmax;
}

.midFooter {
  width: 60%;
  align-self: center;
  text-align: center;
}

.midFooter > h1 {
  font-size: 7vmin;
  font-family: "Roboto";
  color: #eb4034;
}

.midFooter > p {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  margin: 1vmax auto;
  font-size: 1.5vmax;
}

.rightFooter {
  width: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.rightFooter > a {
  margin: 0.5vmax;
  font-size: 1.3vmax;
}

.rightFooter > h4 {
  font-family: "Roboto";
  font-size: 1.4vmax;
  text-decoration: underline;
}

.text-red {
  color: red;
}

.home {
  display: flex;
  min-height: 100vh;
  flex-wrap: wrap;
}

a {
  display: block;
  text-decoration: none;
}

.productCard {
  background-color: white;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 5px 10px;
  cursor: pointer;
  padding: 10px;
}

.productCard img {
  height: 300px;
  width: 200px;
}

.productCard p {
  margin:5px 0;
}

.loading  {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

